import React, { useState, useEffect } from 'react'
import Servers from './services/persons'
import './App.css'

const Filter = ({ filterValue, changeFilterValue }) => (
  <div>
    filter shown with<input value={filterValue} onChange={changeFilterValue} />
  </div>
)

const PersonForm = ({ addPerson, newName, addNewName, newNub, addNewNub }) => (
  <form onSubmit={addPerson}>
    <div>
      name: <input value={newName} onChange={addNewName} />
    </div>
    <div>
      number: <input value={newNub} onChange={addNewNub} />
    </div>
    <div>
      <button type="submit">add</button>
    </div>
  </form>
)

const Persons = ({ persons, deletePerson }) => (
  <>
    {persons.map(person => <p key={person.__id}>{person.name} {person.number} <button onClick={() => deletePerson(person)} >delete</button></p>)}
  </>
)

const Message = ({ message }) => {
  if (!message.type) {
    return null
  }

  const className = message.type === 'message' ? 'message' : 'error'

  return (
    <div className={className}>
      {message.content}
    </div>
  )
}

const App = () => {
  const [persons, setPersons] = useState([])
  const [newName, setNewName] = useState('')
  const [newNub, setNewNub] = useState('')
  const [filterValue, setFilterValue] = useState('')
  const [message, setMessage] = useState({ type: '', content: '' })

  useEffect(() => {
    Servers.getAll().then(data => {
      return setPersons(data)
    })
  }, [])

  const addNewName = (event) => {
    setNewName(event.target.value)
  }

  const addNewNub = (event) => {
    setNewNub(event.target.value)
  }

  const addPerson = (event) => {
    event.preventDefault()
    const isHaveSame = persons.every(person => person.name !== newName)
    if (isHaveSame) {
      const newPerson = {
        name: newName,
        number: newNub
      }

      Servers.create(newPerson).then(data => {
        setPersons(persons.concat(data))
        setMessage({ type: 'message', content: `Add ${newName}` })
        setTimeout(() => {
          setMessage({ type: '', content: '' })
        }, 3000)
      })
    } else {
      const result = window.confirm(`${newName}已经在电话簿里了, 是否要更新其电话号码?`)
      if (result) {
        const p = persons.find(person => person.name === newName)
        const updatePerson = { ...p, number: newNub }
        Servers.update(p._id, updatePerson).then(data => {
          setPersons(persons.map(person => person._id === p._id ? data : person))
          setMessage({ type: 'message', content: `${p.name} is updated` })
          setTimeout(() => {
            setMessage({ type: '', content: '' })
          }, 3000)
        })
          .catch(err => {
            setMessage({ type: 'error', content: `${p.name} update occur error` })
          })
      }
    }
    setNewName('')
    setNewNub('')
  }

  const changeFilterValue = (event) => {
    setFilterValue(event.target.value)
    const filterPersons = persons.filter(person => person.name.toLocaleUpperCase().includes(event.target.value.toLocaleUpperCase()))
    setPersons(filterPersons)
  }

  const deletePerson = person => {
    const result = window.confirm(`确认删除${person.name}`)
    if (result) {
      Servers.destroy(person.__id).then(() => {
        setPersons(persons.filter(p => p.__id !== person.__id))
      })
        .catch(err => {
          setMessage({ type: 'error', content: `Person ${person.name} was already removed from server` })
          setPersons(persons.filter(p => p.__id !== person.__id))
        })
    }
  }

  return (
    <div>
      <h2>Phonebook</h2>
      <Message message={message} />
      <Filter filterValue={filterValue} changeFilterValue={changeFilterValue} />
      <h2>add a new</h2>
      <PersonForm addPerson={addPerson} newName={newName} addNewName={addNewName} newNub={newNub} addNewNub={addNewNub} />
      <h2>Numbers</h2>
      <Persons persons={persons} deletePerson={deletePerson} />
    </div>
  )
}

export default App